import React, {FunctionComponent, MouseEvent, useEffect, useState} from 'react';
import Grid from '@mui/material/Grid';
import Background from './assets/combine-bg.png';
import RightImage from './assets/website-r-for-conbine.png';
import LeftImage from './assets/combine-fox-ears.png';
import FinalImage from './assets/website-r-for-conbine_final.png';
import {ParallaxBanner, useParallax} from "react-scroll-parallax";

const StudentUnion: FunctionComponent = ()=>{

    //Css Change Start...
    const webStyle={
        container: {
            style: {
                height: "120vh",
                position: "relative",
                overflow: "hidden",
                backgroundImage: "url("+Background+")",
                backgroundPosition: "top",
                backgroundRepeat: "noRepeat",
                backgroundSize: "cover",
            },
            gridSpace: 2
        },
        images: {
            container: {
                position: "absolute" as const,
                margin: "55vh 0 0 0",
                width: "100%",
            },
            leftImage: {
                display: "block",
                marginLeft: "auto",
                marginRight: "auto",
                width: "30%",
            },
            rightImage: {
                display: "block",
                marginLeft: "auto",
                marginRight: "auto",
                width: "30%",
            },
            combineImage: {
                display: "block",
                marginLeft: "auto",
                marginRight: "auto",
                width: "30%",
            }
        }
    }

    const mobileStyle={
        container: {
            style: {
                height: "120vh",
                position: "relative",
                overflow: "hidden",
                backgroundImage: "url("+Background+")",
                backgroundPosition: "top",
                backgroundRepeat: "noRepeat",
                backgroundSize: "cover",
            },
            gridSpace: 2
        },
        images: {
            container: {
                position: "absolute" as const,
                margin: "70vh 0 0 0",
                width: "100%",
            },
            leftImage: {
                display: "block",
                marginLeft: "auto",
                marginRight: "auto",
                width: "70%",
            },
            rightImage: {
                display: "block",
                marginLeft: "auto",
                marginRight: "auto",
                width: "70%",
            },
            combineImage: {
                display: "block",
                marginLeft: "auto",
                marginRight: "auto",
                width: "70%",
            }
        }
    }

    const changeLayout = ()=>{
        return window.innerWidth > 900 ? webStyle : mobileStyle;
        // return webStyle;
    }

    const [combinationStyle, setCombinationStyle] = useState(changeLayout());

    window.addEventListener('resize', ()=>{
        setCombinationStyle(changeLayout());
    });
    //Css Change End.

    const leftImage = useParallax<HTMLDivElement>({
        speed: 10,
        translateX: [50, -8, 'easeOutQuint'],
        translateY: [0, -50, 'easeOutQuad'],
        opacity: [2,-4],
    });

    const rightImage = useParallax<HTMLDivElement>({
        speed: 10,
        translateX: [-50, 8, 'easeOutQuint'],
        translateY: [0, -50, 'easeOutQuad'],
        opacity: [2,-4],
    });

    const combineImage = useParallax<HTMLDivElement>({
        speed: 10,
        translateX: [0, 0, 'easeOutQuint'],
        translateY: [0, -50, 'easeOutQuad'],
        opacity: [-4,10],
    });


    return (
        <Grid id={"dressup"}
              container
              spacing={combinationStyle.container.gridSpace}
              sx={combinationStyle.container.style}>
            <Grid item xs={12} style={{
                textAlign: "center",
                margin: "10vh 0 0 0",
                backgroundColor: "rgba(255,255,255, 0.5)",
            }}>
                <h1 style={{fontFamily: "BaksoSapi"}}>How to dress up your SOLC?</h1>
                <p style={{
                    fontFamily: "FuturaHandwritten"
                }}>
                    After minting your SOLC, you can dress her up with our accessory NFTs!
                    You can get these accessories by joining our events and giveaways.
                </p>
            </Grid>
            <div ref={rightImage.ref} style={combinationStyle.images.container}>
                <img src={RightImage} style={combinationStyle.images.rightImage}/>
            </div>
            <div ref={leftImage.ref} style={combinationStyle.images.container}>
                <img src={LeftImage} style={combinationStyle.images.leftImage}/>
            </div>
            <div ref={combineImage.ref} style={combinationStyle.images.container}>
                <img src={FinalImage} style={combinationStyle.images.combineImage}/>
            </div>

        </Grid>
    );
}

export default StudentUnion;